<template>
  <div class="home">
    <br>
    <center>
        <v-flex>
         <v-card max-width="600px">
           <v-card-title>
             <h2>Report</h2>
           </v-card-title>
           <v-card-text class="px-3">
             <v-text-field label="School Site" v-model="schoolSite"></v-text-field>
           </v-card-text>
           <v-menu class="px-3" ref="menu" v-model="menu" :close-on-content-click="false" :return-value.sync="date" transition="scale-transition" offset-y max-width="290px" min-width="290px">
             <template v-slot:activator="{ on }">
               <v-text-field class="px-3" v-model="date" label="Date of Visit" readonly v-on="on"></v-text-field>
             </template>
             <v-date-picker v-model="date" no-title scrollable>
               <v-spacer></v-spacer>
               <v-btn text color="primary" @click="menu = false">Cancel</v-btn>
               <v-btn text color="primary" @click="$refs.menu.save(date)">OK</v-btn>
             </v-date-picker>
           </v-menu>
           <v-card-text class="px-3">
             <v-textarea label="Site Recap" v-model="recap"> </v-textarea>
           </v-card-text>
           <v-spacer></v-spacer>
           <v-btn color="primary" @click="createPDF">Create PDF</v-btn>
           <v-card-text></v-card-text>
         </v-card>
        </v-flex>
    </center>
  </div>
</template>

<script>
// @ is an alias to /src
import htmlPDF from './PdfHtml'



export default {

  name: 'home',
    data() {
      return {
          date: new Date().toISOString().substr(0, 10),
          menu: false,
          modal: false,
          recap: '',
          schoolSite: '',
      }
    },
    methods: {
      createPDF: function () {
          // Sent to pdfHTML view page - comment it out and saving a click to create a pdf:
          // Leave just in case I want to do something different on that page:
          // this.$router.push({name: htmlPDF, params: {schoolSite: this.schoolSite, date: this.date, recap: this.recap}})

          // Load external scripts first and chain to promise
          this.$loadScript('https://pagecdn.io/lib/html2canvas/0.4.1/html2canvas.min.js')
              .then(( ) => {
                  this.$loadScript('https://cdnjs.cloudflare.com/ajax/libs/jspdf/1.5.3/jspdf.debug.js')
                      .then(( ) => {
                          let pdf = jsPDF();
                          let img = "data:image/png;base64,"

                          pdf.addImage(img, 'png', 65, 0);
                          pdf.setFontSize(22);
                          pdf.text(20,50,`End of Day Report - ${this.schoolSite}`, {maxWidth: 175});

                          pdf.setFontSize(14);
                          pdf.text(20,60,`Date: ${this.date}`);

                          let recap = this.recap.split('\n');
                          let y = 80;

                          if (recap.length == 0){
                              pdf.text(20, 70, `Recap:`, {maxWidth: 175});
                              pdf.text(20, y, `${this.recap}`, {maxWidth: 175});
                          } else {
                              recap.forEach((p ) => {
                                  if(p !== ''){
                                      if (p.length > 70){
                                          pdf.text(20, y, `${p}`, {maxWidth: 175});
                                          let num = Math.floor(p.length / 90);
                                          let addNum = Math.floor((num) * 10);
                                          y += addNum;
                                      } else {
                                          pdf.text(20, y, `${p}`, {maxWidth: 175});
                                          y += 10;
                                      }
                                  }
                              })
                          }
                          pdf.output('dataurlnewwindow', {filename: `${this.schoolSite}-${this.date}`});
                      })
              })
      }
    },
  components: {
  }
}
</script>
